import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import ReactDOM from 'react-dom';
import Modal from 'react-modal';
import AddIcon from '@material-ui/icons/Add';
import bootstrapTable from 'bootstrap-table';
import { SnackBar, Input, Button } from '../components';
import { SettingsNavbar } from './components';
import { RetriveMyTransactions } from './SettingService';
import './setting.css';

const customStyles = {
  content : {
    top                   : '50%',
    left                  : '50%',
    right                 : 'auto',
    bottom                : 'auto',
    marginRight           : '-50%',
    transform             : 'translate(-50%, -50%)'
  }
};
Modal.setAppElement('#root');

class Transactions extends Component {
  state = {
    transactions:[],
    envois:[],
    receptions:[],
    snackbarOpen: false,
    modalIsOpen: false,
  };

  componentWillMount(){

  }


  componentDidMount(){
    var aScript = document.createElement('script');
    aScript.type = 'text/javascript';
    aScript.src = "https://unpkg.com/bootstrap-table@1.14.1/dist/bootstrap-table.min.js";

    document.head.appendChild(aScript);
    this.retrieveMyTransactions();
  }

  displaySnackbar = () => {
    this.setState({snackbarOpen: true,});
  };

  handleSnackBarClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    this.setState({ snackbarOpen: false });
  };

  openModal = () => {
    this.setState({modalIsOpen: true});
  }

  afterOpenModal = () => {

  }

  closeModal = () => {
    this.setState({modalIsOpen: false});
  }

  retrieveMyTransactions = () => {
    RetriveMyTransactions().then(response => {
      let myTransactions = response.data;
      console.log(response);
      console.log(myTransactions);
      console.log("succes");
      this.setState({
        transactison: myTransactions,
      });
    })
    .catch(error => {
      console.log("error");
      console.log(error);
      this.setState({
        snackbarOpen: true,
      });
    });
  }

  render() {
    let envois = this.state.envois;
    let receptions = this.state.receptions;
    let snackbarOpen = this.state.snackbarOpen;
    return (
      <div>
        <SettingsNavbar />
        <div className="jumbotron">
          <hr className="hr" align="left"/>
          <h2>Mes Transactions</h2>
        </div>
        <div className="container table-container">

          <h3 align="left" className="margin-top-20">Envois</h3>
          <div className="pull-left send-credit-button" onClick={this.openModal}><AddIcon/>Envoyer du crdit</div>
          <table data-toggle="table"
                 data-pagination="true"
                 data-search="true">
            <thead className="color-black">
              <tr>
                <th data-sortable="true">Destinataires</th>
                <th data-sortable="true">Nombres de crdits</th>
                <th data-sortable="true">Dates</th>
              </tr>
            </thead>
            <tbody className="color-black">
              {envois.map(item => (
                <tr>
                  <td>1</td>
                  <td>Item 1</td>
                  <td>$1</td>
                </tr>
              ))}
            </tbody>
          </table>

          <h3 align="left" className="margin-top-20">Rceptions</h3>
          <table data-toggle="table"
                 data-pagination="true"
                 data-search="true">
            <thead>
              <tr className="color-black">
                <th data-sortable="true">Emetteurs</th>
                <th data-sortable="true">Nombres de crdits</th>
                <th data-sortable="true">Dates</th>
              </tr>
            </thead>
            <tbody className="color-black">
              {receptions.map(item => (
                <tr>
                  <td>1</td>
                  <td>Item 1</td>
                  <td>$1</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
        <Modal
          isOpen={this.state.modalIsOpen}
          onAfterOpen={this.afterOpenModal}
          onRequestClose={this.closeModal}
          style={customStyles}
          contentLabel="Send credit modal"
        >

          <h2>Votre solde</h2>
          <h3>10</h3>
          <div></div>
          <form>
            <Input type="email" name="email" placeholder="Email destinataire"/>
            <Input type="number" name="credit" placeholder="Nombre de credits"/>
            <Button text="Envoyer"/>
          </form>
        </Modal>
        <SnackBar vertical={"bottom"}
                  horizontal={"center"}
                  open={snackbarOpen}
                  duration={6000}
                  message={"Vous n'avez pas de crdits, veillez d'abord recharger votre compte."}
                  onClose={this.handleSnackBarClose}/>
      </div>
    );
  }

}

export default Transactions;
