import React, { Component } from 'react';
import { Link, Redirect } from 'react-router-dom';
import { AuthenticationNavbar } from './components';
import { AuthInput, AuthButton } from './components';
import { RegisterUser } from './AuthService';
import SnackBar from './components/SnackBar';
import './Auth.css';


class Register extends Component {
  state = {
    snackbarOpen: false,
  };

  handleSnackBarClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    this.setState({ snackbarOpen: false });
  };

  formSubmitHandler = event => {
    event.preventDefault();
    let data = {
        "user": {
          "first_name": event.target.username.value.toString(),
          "last_name": event.target.usersurname.value.toString(),
          "email": event.target.useremail.value.toString(),
          "password": event.target.password.value.toString()
        },
        "phoneNumber": "------",
        "country": "CM",
        "town": "Yaounde",
        "avatar": "avatar",
        "activitySector": "none",
        "numberCredits": 0
    };
    RegisterUser(data).then(response => {
      this.props.history.push('/Verify-email');
    })
    .catch(error => {
      this.setState({
        snackbarOpen: true,
      });
    });
  };

  render() {
    let snackbarOpen = this.state.snackbarOpen;
    return (
      <div className="auth-page">
        <AuthenticationNavbar />
        <div className="d-flex flex-column justify-content-center auth-box">
            <div className="auth-form-box">
              <h1>Crer un compte</h1>
              <form className="d-flex flex-column justify-content-center auth-form-contain" onSubmit={this.formSubmitHandler}>
                <AuthInput type='text' name='username' placeholder='Nom'/>
                <AuthInput type='text' name='usersurname' placeholder='Prnom'/>
                <AuthInput type='email' name='useremail' placeholder='Email'/>
                <AuthInput type='password' name='password' placeholder='Mot de passe'/>
                <AuthButton text='Crer' />
              </form>
            </div>
        </div>
        <SnackBar vertical={"bottom"}
                  horizontal={"center"}
                  open={snackbarOpen}
                  duration={3000}
                  message={"Une erreur est survenue, veillez ressayer."}
                  onClose={this.handleSnackBarClose}/>
      </div>
    );
  }

}

export default Register;
