import React, { Component } from 'react';
import { Link, Redirect } from 'react-router-dom';
import { AuthenticationNavbar } from './components';
import { AuthInput, AuthButton } from './components';
import { LoginUser } from './AuthService';
import SnackBar from './components/SnackBar';
import './Auth.css';


class Login extends Component {
  state = {
    snackbarOpen: false,
  };

  handleSnackBarClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    this.setState({ snackbarOpen: false });
  };

  formSubmitHandler = event => {
    event.preventDefault();
    let data = new FormData();
    data.set('email', event.target.useremail.value.toString());
    data.set('password', event.target.userpassword.value.toString());

    LoginUser(data).then(response => {
      localStorage.setItem("token",response.data.token);
      this.props.history.push('/My-Pubpoints');
    })
    .catch(error => {
      this.setState({
        snackbarOpen: true,
      });
    });
  };

  render() {
    let snackbarOpen = this.state.snackbarOpen;
    return (
        <div className="auth-page">
          <AuthenticationNavbar />
          <div className="d-flex flex-column justify-content-center auth-box">
              <div className="auth-form-box">
                <h1>Se connecter</h1>
                <form className="d-flex flex-column justify-content-center auth-form-contain" onSubmit={this.formSubmitHandler}>
                  <AuthInput type='email' name='useremail' placeholder='E-mail' />
                  <AuthInput type='password' name='userpassword' placeholder='Mot de passe' />
                  <AuthButton text='Dmarrer' />
                </form>
              </div>
              <Link to="/Reset-password" className="password-forgotten">Mot de passe oubli ?</Link>
          </div>
          <SnackBar vertical={"bottom"}
                    horizontal={"center"}
                    open={snackbarOpen}
                    duration={3000}
                    message={"Une erreur est survenue, veillez ressayer."}
                    onClose={this.handleSnackBarClose}/>
        </div>
    );
  }

}

export default Login;
