import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import AddIcon from '@material-ui/icons/Add';
import { SettingsNavbar, AnimationCard } from './components';
import { RetriveMyPubPoints } from './SettingService';
import SnackBar from '../components/SnackBar';
import Footer from '../components/Footer';
import './setting.css';

class Projects extends Component {
  /**
  * Variable d'tat du composant.
  */
  state = {
    snackbarOpen: false,
    animation: {},
  };

  componentDidMount(){
    this.retriveMyPubPoints();
  }

  /**
  * Gestionnaire de la fermeture du Snackbar pour l'affichage du message d'erreur.
  */
  handleSnackBarClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    this.setState({ snackbarOpen: false });
  };

  /**
  * Gestionnaire de la collecte des PubPoints de l'utilisateur courant.
  */
  retriveMyPubPoints = () => {
    RetriveMyPubPoints().then(response => {
      let myPubpoint = response.data;
      console.log(response);
      console.log(myPubpoint);
      console.log("succes");
      this.setState({
        animation: myPubpoint,
      });
    })
    .catch(error => {
      console.log("error");
      console.log(error);
      this.setState({
        snackbarOpen: true,
      });
    });
  };

  /**
  * Gestionnaire de rendu de la vue du composant.
  */
  render() {
    let snackbarOpen = this.state.snackbarOpen;
    let animation = this.state.animation;
    return (
      <div>
        <SettingsNavbar />
        <div className="jumbotron">
          <hr className="hr" align="left"/>
          <h2>Mes PubPoints</h2>
        </div>

        <div className="container">
          <Link to="/Pubpoint-studio" className="new-pubpoint-link pull-left"><AddIcon />Crer un PubPoint</Link>

        </div>
        <SnackBar vertical={"bottom"}
                  horizontal={"center"}
                  open={snackbarOpen}
                  duration={3000}
                  message={"Nous n'arrivons pas  charger vos PubPoints."}
                  onClose={this.handleSnackBarClose}/>
      </div>
    );
  }

}

export default Projects;
